<template>
    <div class="min-h-screen bg-gray-50">
        <div class="container mx-auto p-6">
            <!-- Header -->
            <div class="flex items-center justify-between mb-8 bg-gray-200/50 rounded-lg p-4 animate-fade-in-down">
                <h1 class="text-2xl font-semibold text-gray-800">New Product</h1>
            </div>

            <!-- Form Container -->
            <div class="bg-white rounded-lg shadow-sm border border-gray-200 p-8 animate-fade-in-up">
                <form @submit.prevent="handleSubmit">
                    <!-- Basic Information -->
                    <div class="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
                        <div>
                            <label class="block text-sm font-medium text-gray-700 mb-1">
                                Title <span class="text-red-500">*</span>
                            </label>
                            <input type="text" v-model="form.title"
                                class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            <p v-if="errors.title" class="text-xs text-red-500 mt-1">Title is required</p>
                        </div>

                        <div>
                            <label class="block text-sm font-medium text-gray-700 mb-1">
                                Name <span class="text-red-500">*</span>
                            </label>
                            <input type="text" v-model="form.name"
                                class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            <p v-if="errors.name" class="text-xs text-red-500 mt-1">Name is required</p>
                        </div>

                        <div>
                            <label class="block text-sm font-medium text-gray-700 mb-1">Code</label>
                            <input type="text" v-model="form.code" placeholder="ex: PS-101"
                                class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                        </div>
                    </div>

                    <!-- Category, Serial, Unit -->
                    <div class="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
                        <div>
                            <label class="block text-sm font-medium text-gray-700 mb-1">
                                Category <span class="text-red-500">*</span>
                            </label>
                            <input type="text" v-model="form.category_uuid" placeholder="Enter Category UUID"
                                class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            <p v-if="errors.category_uuid" class="text-xs text-red-500 mt-1">Category is required</p>
                        </div>

                        <div>
                            <label class="block text-sm font-medium text-gray-700 mb-1">Serial No.</label>
                            <input type="text" v-model="form.serial_no"
                                class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                        </div>

                        <div>
                            <label class="block text-sm font-medium text-gray-700 mb-1">
                                Unit <span class="text-red-500">*</span>
                            </label>
                            <input type="text" v-model="form.unit" placeholder="ex: pcs, kg, box"
                                class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            <p v-if="errors.unit" class="text-xs text-red-500 mt-1">Unit is required</p>
                        </div>
                    </div>

                    <!-- Pricing Section -->
                    <div class="mb-8">
                        <h3 class="text-lg font-semibold text-gray-800 mb-4">Pricing</h3>
                        <div class="grid grid-cols-1 md:grid-cols-4 gap-4">
                            <div>
                                <label class="block text-sm font-medium text-gray-700 mb-1">Purchase Price</label>
                                <input type="number" v-model.number="form.purchase_price" min="0" step="0.01"
                                    class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            </div>
                            <div>
                                <label class="block text-sm font-medium text-gray-700 mb-1">Sales Price</label>
                                <input type="number" v-model.number="form.sales_price" min="0" step="0.01"
                                    class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            </div>
                            <div>
                                <label class="block text-sm font-medium text-gray-700 mb-1">MRP</label>
                                <input type="number" v-model.number="form.mrp" min="0" step="0.01"
                                    class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            </div>
                            <div>
                                <label class="block text-sm font-medium text-gray-700 mb-1">Max Stock Alert</label>
                                <input type="number" v-model.number="form.max_stock_alert" min="0"
                                    class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            </div>
                        </div>
                    </div>

                    <!-- Stock Section -->
                    <div class="mb-8">
                        <h3 class="text-lg font-semibold text-gray-800 mb-4">Stock</h3>
                        <div class="grid grid-cols-1 md:grid-cols-3 gap-4">
                            <div>
                                <label class="block text-sm font-medium text-gray-700 mb-1">Initial Stock</label>
                                <input type="number" v-model.number="form.initial_stock" min="0"
                                    class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            </div>
                            <div>
                                <label class="block text-sm font-medium text-gray-700 mb-1">Batch</label>
                                <input type="text" v-model="form.batch"
                                    class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            </div>
                            <div>
                                <label class="block text-sm font-medium text-gray-700 mb-1">Size</label>
                                <input type="text" v-model="form.size"
                                    class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                            </div>
                        </div>
                    </div>

                    <!-- Dates & Description -->
                    <div class="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
                        <div>
                            <label class="block text-sm font-medium text-gray-700 mb-1">Mfg Date</label>
                            <input type="date" v-model="form.mfg_date"
                                class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                        </div>
                        <div>
                            <label class="block text-sm font-medium text-gray-700 mb-1">Exp Date</label>
                            <input type="date" v-model="form.exp_date"
                                class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500" />
                        </div>
                    </div>
                    <div class="flex flex-wrap gap-6">
                        <!-- Description -->
                        <div class="flex-1 min-w-[300px]">
                            <label class="block text-sm font-medium text-gray-700 mb-1">Description</label>
                            <textarea v-model="form.description" rows="4"
                                class="w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500"></textarea>
                        </div>

                        <!-- Image Upload (KEEP YOUR DESIGN) -->
                        <div class="flex-1 min-w-[300px]">
                            <label class="block text-sm font-medium text-gray-700 mb-1">Image</label>
                            <div class="border-2 border-dashed border-gray-300 rounded-lg p-12 text-center cursor-pointer hover:border-gray-400 transition-colors"
                                @click="triggerFileUpload" @dragover.prevent="handleDragOver"
                                @dragleave="handleDragLeave" @drop.prevent="handleDrop"
                                :class="{ 'border-blue-400 bg-blue-50': isDragging }">

                                <div v-if="!imagePreview" class="text-gray-400">
                                    <svg class="mx-auto h-12 w-12 mb-2" stroke="currentColor" fill="none"
                                        viewBox="0 0 48 48">
                                        <path
                                            d="M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02"
                                            stroke-width="2" stroke-linecap="round" stroke-linejoin="round" />
                                    </svg>
                                    <p class="text-sm text-gray-500">Click to upload or drag and drop</p>
                                    <p class="text-xs text-gray-400 mt-1">PNG, JPG, GIF up to 10MB</p>
                                </div>

                                <div v-else class="space-y-2">
                                    <img :src="imagePreview" alt="Preview"
                                        class="max-w-full h-32 object-cover rounded-md mx-auto" />
                                    <button type="button" @click.stop="removeImage"
                                        class="text-sm text-red-600 hover:text-red-700">
                                        Remove Image
                                    </button>
                                </div>

                                <input ref="fileInput" type="file" class="hidden" accept="image/*"
                                    @change="handleImageUpload" />
                            </div>
                        </div>
                    </div>


                    <!-- Active -->
                    <div class="mb-8 flex items-center gap-2">
                        <input type="checkbox" v-model="form.is_active" />
                        <label class="text-sm text-gray-700">Active</label>
                    </div>

                    <!-- Submit -->
                    <div class="flex justify-end gap-4">
                        <button type="button" @click="resetForm"
                            class="px-4 py-2 border border-gray-300 rounded-md bg-white hover:bg-gray-50">
                            Cancel
                        </button>
                        <button type="submit"
                            class="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50"
                            :disabled="!isFormValid">
                            Save Product
                        </button>
                    </div>

                </form>
            </div>
        </div>
    </div>
</template>

<script setup>
import { reactive, ref, computed } from "vue";
import { v4 as uuidv4 } from "uuid";
import { useRouter } from "vue-router";

const router = useRouter();
const form = reactive({
    title: "",
    name: "",
    code: "",
    category_uuid: "",
    serial_no: "",
    unit: "",
    purchase_price: 0,
    sales_price: 0,
    mrp: 0,
    max_stock_alert: 0,
    discount: 0,
    vat: 0,
    wholesale_price: 0,
    wholesale_min_qty: 0,
    initial_stock: 0,
    batch: "",
    size: "",
    mfg_date: "",
    exp_date: "",
    description: "",
    asset: "",
    is_active: true,
    image: null,
});

const errors = reactive({
    title: false,
    name: false,
    category_uuid: false,
    unit: false,
});

const imagePreview = ref(null);

const isFormValid = computed(
    () =>
        form.title.trim() &&
        form.name.trim() &&
        form.category_uuid.trim() &&
        form.unit.trim()
);

const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = (ev) => {
        imagePreview.value = ev.target.result;
        form.image = ev.target.result;
    };
    reader.readAsDataURL(file);
};

const resetForm = () => {
    Object.keys(form).forEach((key) => (form[key] = key === "is_active" ? true : ""));
    imagePreview.value = null;
    Object.keys(errors).forEach((k) => (errors[k] = false));
};

const handleSubmit = () => {
    // Validate
    errors.title = !form.title.trim();
    errors.name = !form.name.trim();
    errors.category_uuid = !form.category_uuid.trim();
    errors.unit = !form.unit.trim();
    if (!isFormValid.value) return alert("Please fill all required fields");

    // Prepare product
    const newProduct = { ...form, uuid: uuidv4(), id: Date.now() };

    // Get existing products
    const products = JSON.parse(localStorage.getItem("products")) || [];
    products.push(newProduct);
    localStorage.setItem("products", JSON.stringify(products));

    alert("Product added successfully!");
    router.push("/productList"); // redirect to product list
};
</script>
